import React, { useState, useEffect } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import {
  Content,
  Header,
  Page,
  pageTheme,
  ContentHeader,
} from '@backstage/core';
import { makeStyles, createStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControl from '@material-ui/core/FormControl';
import { Alert } from '@material-ui/lab';
import { DefaultApi } from '../../api/apis';

import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';

import Select from '@material-ui/core/Select';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles((theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'center',
    },
    margin: {
      margin: theme.spacing(3),
    },
    withoutLabel: {
      marginTop: theme.spacing(3),
    },
    textField: {
      width: '25ch',
    },
  }),
);

const initialUserState = {
  dOCTORID: 'D0001',
  dOCTORNAME: ' ',
  dOCTOREMAIL: 'Gumlung@gmail.com',
};

const username = { givenuser: ' ' };

export default function Create() {
  const classes = useStyles();
  const profile = { givenName: ' ' };
  const api = new DefaultApi();

  const [deceased, setDeceased] = useState(String);
  const [status, setStatus] = useState(false);
  const [alert, setAlert] = useState(true);
  const [coolrooms, setCoolrooms] = useState(Array);
  const [coolroomtypes, setCoolroomTypes] = useState(Array);
  const [relatives, setRelatives] = useState(Array);
  const [loading, setLoading] = useState(true);
  const [deathtime, setDeathtime] = useState(String);

  const [coolroomid, setcoolroom] = React.useState(Number);
  const [typeid, setDeadStatus] = React.useState(Number);
  const [relativeid, setRelative] = React.useState(Number);

  useEffect(() => {
    const getCoolroomTypes = async () => {
      const res = await api.listCoolroomtype({ limit: 10, offset: 0 });
      setLoading(false);
      setCoolroomTypes(res);
    };
    getCoolroomTypes();

    const getCoolrooms = async () => {
      const res = await api.listCoolroom({ limit: 10, offset: 0 });
      setLoading(false);
      setCoolrooms(res);
    };
    getCoolrooms();

    const getRelatives = async () => {
      const res = await api.listRelative({ limit: 10, offset: 0 });
      setLoading(false);
      setRelatives(res);
    };
    getRelatives();

  }, [loading]);

  const handleInputChange = (event) => {
    //const { id, value } = event.target;
    //setDeceased({ ...user, [id]: value });
    setDeceased(event.target.value );
  };

  const handleDeathtimeChange = (event) => {
    setDeathtime(event.target.value );
  };

  const CoolroomhandleChange = (event) => {
    setcoolroom(event.target.value );
  };

  const DeathtypehandleChange = (event) => {
    setDeadStatus(event.target.value );
  };

  const RelativehandleChange = (event) => {
    setRelative(event.target.value );
  };

  const CreateDeceasedReceive = async () => {
    const deceasedreceive = {
      dECEASEDID: 1 ,
      dECEASEDNAME: "deceased",
      edges: {coolroomid: {id: 1}, doctorid: {id: 1}, relativeid: {id: 1}},
      dEATHTIME: "deathtime"
    };
    const res = await api.createDeceasedreceive({ deceasedreceive: deceasedreceive });
    setStatus(true);
    if (res.id != '') {
      setAlert(true);
    } else {
      setAlert(false);
    }
    const timer = setTimeout(() => {
      setStatus(false);
    }, 1000);
  };

  return (
    React.createElement(Page, { theme: pageTheme.home,}
      , React.createElement(Header, {
        title: `${profile.givenName}`,}
      //subtitle="Some quick intro and links."
      )
      , React.createElement(Content, null
        , React.createElement(ContentHeader, { title: "",}
          , React.createElement(Typography, { align: "left", style: { marginRight: 16, color: "#00eeff" },}
            , username.givenuser
          )
          , React.createElement('div', null
            , React.createElement(Button, { variant: "contained", color: "primary",}, ""

         )
          )
          , status ? (
            React.createElement('div', null
              , alert ? (
                React.createElement(Alert, { severity: "success",}, "This is a success alert  check it out!"

                )
              ) : (
                  React.createElement(Alert, { severity: "warning", style: { marginTop: 20 },}, "This is a warning alert  check it out!"

                  )
                )
            )
          ) : null
        )
        , React.createElement('div', { className: classes.root,}
          , React.createElement('form', { noValidate: true, autoComplete: "off",}
            , React.createElement(FormControl, {
              //fullWidth
              className: classes.margin,
              variant: "outlined",}
            
              , React.createElement(TextField, {
                id: "name",
                label: "-",
                variant: "outlined",
                type: "string",
                size: "medium",
                value: deceased,
                onChange: handleInputChange,
                style: { width: 400 },}
              )
            )

            , React.createElement('div', null
              , React.createElement(FormControl, {
                className: classes.margin,
                variant: "outlined",}
              
                , React.createElement(InputLabel, { id: "relative-label",}, "- " )
                , React.createElement(Select, {
                  labelId: "relative-label",
                  id: "relative",
                  value: relativeid,
                  onChange: RelativehandleChange,
                  style: { width: 400 },}
                
                , relatives.map((item) => (
                  React.createElement(MenuItem, { value: item.id,}, item.rELATIVENAME)
                ))
                )
              )
            )

            , React.createElement(FormControl, {
              className: classes.margin,
              variant: "outlined",}
            
              , React.createElement(InputLabel, { id: "status-death",}, "")
              , React.createElement(Select, {
                labelId: "status-death",
                id: "statusdeath",
                value: typeid,
                onChange: DeathtypehandleChange,
                style: { width: 200 },}
              
                , coolroomtypes.map((item) => (
                  React.createElement(MenuItem, { value: item.id,}, item.cOOLROOMTYPENAME)
                ))
              )
            )

            , React.createElement('div', null
              , React.createElement(FormControl, {
                className: classes.margin,
                variant: "outlined",}
              
                , React.createElement(InputLabel, { id: "coolroom-label",}, "")
                , React.createElement(Select, {
                  labelId: "coolroom-label",
                  id: "coolroom",
                  value: coolroomid,
                  onChange: CoolroomhandleChange,
                  style: { width: 200 },}
                
                , coolrooms.filter((setfilterid ) => setfilterid.id === typeid).map((item) => (
                  React.createElement(MenuItem, { value: item.id,}, item.cOOLROOMNAME)                 
                ))
                )
              )

              , React.createElement(FormControl, {
                className: classes.margin,
                variant: "outlined",}
              
                , React.createElement(TextField, {
                  id: "deathtime",
                  label: "// " ,
                  type: "datetime-local",
                  value: deathtime,
                  onChange: handleDeathtimeChange,
                  className: classes.textField,
                  InputLabelProps: {
                    shrink: true,
                  },}
                )
              )
            )

            , React.createElement('div', { className: classes.margin,}
              , React.createElement(Button, {
                onClick: () => {
                  CreateDeceasedReceive();
                },
                variant: "contained",
                color: "primary",}
              , ""

             )
              , React.createElement(Button, {
                style: { marginLeft: 20 },
                component: RouterLink,
                to: "/",
                variant: "contained",}
              , ""

             )
            )
          )
        )
      )
    )
  );
}
