import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import { DefaultApi } from '../../api/apis';
 
const useStyles = makeStyles({
 table: {
   minWidth: 650,
 },
});
 
export default function ComponentsTable() {
 const classes = useStyles();
 const api = new DefaultApi();
 const [deceasedreceives, setDeceasedreceives] = useState(Array);
 const [loading, setLoading] = useState(true);
 
 useEffect(() => {
   const getDeceasedreceives = async () => {
     const res = await api.listDeceasedreceive({ limit: 10, offset: 0 });
     setLoading(false);
     setDeceasedreceives(res);
   };
   getDeceasedreceives();
 }, [loading]);
 
 const deleteDeceasedreceives = async (id) => {
   const res = await api.deleteDeceasedreceive({ id: id });
   setLoading(true);
 };
 
 return (
   React.createElement(TableContainer, { component: Paper,}
     , React.createElement(Table, { className: classes.table, 'aria-label': "simple table" ,}
       , React.createElement(TableHead, null
         , React.createElement(TableRow, null
           , React.createElement(TableCell, { align: "center",}, "")
           , React.createElement(TableCell, { align: "center",}, "-")
           , React.createElement(TableCell, { align: "center",}, "- " )
           , React.createElement(TableCell, { align: "center",}, "")
           , React.createElement(TableCell, { align: "center",}, "// " )
           , React.createElement(TableCell, { align: "center",}, "")
           , React.createElement(TableCell, { align: "center",}, "")
         )
       )
       , React.createElement(TableBody, null
         , deceasedreceives.map((item) => (
           React.createElement(TableRow, { key: item.id,}
             , React.createElement(TableCell, { align: "center",}, item.id)
             , React.createElement(TableCell, { align: "center",}, item.dECEASEDNAME)
             , React.createElement(TableCell, { align: "center",})
             , React.createElement(TableCell, { align: "center",})
             , React.createElement(TableCell, { align: "center",}, item.dEATHTIME)
             , React.createElement(TableCell, { align: "center",})
             , React.createElement(TableCell, { align: "center",}
               , React.createElement(Button, {
                 onClick: () => {
                   deleteDeceasedreceives(item.id);
                 },
                 style: { marginLeft: 10 },
                 variant: "contained",
                 color: "secondary",}
               , ""

               )
             )
           )
         ))
       )
     )
   )
 );
}